body {
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    font-family: "Roboto", sans-serif;
}

.game-display {
    display: flex;
    justify-content: space-evenly;
    width: 100vw;
    height: 100vh;
    align-items: center;
}

.player-card {
    width: 170px;
    height: 75px;
    display: flex;
    flex-direction: column;
    padding: 1em;
    font-size: 32px;
    border-radius: 0.2em;
    margin:0.75em;
}

.options {
    min-width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.board {
    width: 900px;
    height: 900px;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%);
    background-color: #fcfcfc;
}

.corner {
    width: 100px;
    height: 100px;
    background-color: white;
    border: 1px solid black;
    text-align: center;
}

.corner:hover {
    background-color: rgb(185, 247, 255);
}

.corner-tile-players {
    position: absolute;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
    width: 100px;
    height: 100px;
}

.horizontal-row {
    display: flex;
}

.horizontal-tile {
    width: 76px;
    height: 100px;
    background-color: white;
    border: 1px solid black;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.horizontal-tile:hover {
    background-color: rgb(185, 247, 255) !important;
}

.row {
    display: flex;
    position: relative;
    justify-content: space-evenly;
    width: 700px;
    height: 100px;
    transform-origin: center;
}

.property-header {
    width: 100%;
    height: 20px;
    background-color: #00ceff;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.property-house {
    width: 12px;
    height: 12px;
    background-color: #0bbd6b;
}

.property-hotel {
    width: 20px;
    height: 15px;
    background-color: #dd2828;
}

.horizontal-tile-players {
    position: absolute;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
    width: 76px;
    height: 100px;
}

.horizontal-tile-player {
    width: 15px;
    height: 15px;
    border-radius: 1em;
    background-color: #000000;
}

.middle-row {
    width: 900px;
    height: 700px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.seperation {
    height: 900px;
    width: 700px;
}

.column {
    height: 700px;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

/*----*/

.join-page {

    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: gray url(https://subtlepatterns.com/patterns/geometry2.png) repeat 0 0;
    animation: slide 20s linear infinite;
}

@keyframes slide {
    from { background-position: 0 0; }
    to { background-position: -400px 0; }
}

.game-page {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: contain;
    background-size: cover;
    background: gray url(https://subtlepatterns.com/patterns/geometry2.png) repeat 0 0;
    animation: slide 20s linear infinite;
    /*background-image: url("https://cdn.shopify.com/s/files/1/1918/9937/products/monopoly-money-stack-hasbro-fabric-camelot-fabrics.jpg?v=1610215258");*/
}

.center-board{
    display:flex;
    flex-direction: column;
    justify-content: center;
}

.menu {
    background-color: #fcfcfc;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%);
    margin: 2em;
    padding: 3em;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-width: 300px;
    max-width: 300px;
    min-height: 400px;
    max-height: fit-content;
}

.menu-button {
    background-color: #313131; /* Green */
    border: none;
    color: white;
    padding: 0.75em 3em;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 0.5em;
    min-width: 150px;
    border-radius: 4px;
}

.menu-button:hover {
    background-color: rgb(80, 80, 80);
}

.menu-title {
    font-size: 24px;
    /*margin-top: 1em;*/
    color: #555555;
    text-decoration: none;
}

.menu-subtitle {
    font-size: 12px;
    margin: 1em;
    color: #555555;
    text-decoration: none;
}

.menu-input {
    border: 1px solid #555555;
    padding: 0.75em 0.75em;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 0.5em;
}

/* Property Detailed */

.property-detailed {
    border: 1px solid black;
    padding: 0.75em;
    min-width: 175px;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%);
    background-color: white;
}

.property-detailed-header {
    padding: 0.5em;
    text-align: center;
    border: 1px solid black;
}

.property-detailed-rent {
    font-size: 0.75em;
    margin: 1em;
    text-align: center;
}
.property-detailed-row {
    display: flex;
    justify-content: space-between;
}

