/* modal */
.modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    text-align: left;
    background: rgba(0, 0, 0, 0.9);
    transition: opacity 0.25s ease;
}

.modal-bg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.modal-state {
    display: none;
}

.modal-state:checked + .modal {
    opacity: 1;
    visibility: visible;
}

.modal-state:checked + .modal .modal-inner {
    top: 0;
}

.modal-inner {
    transition: top 0.25s ease;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 30%;
    margin: auto;
    overflow: auto;
    background: #fff;
    border-radius: 5px;
    padding: 1em 2em;
    height: fit-content;
    /*min-height: 25%;
    max-height: fit-content;*/
}

.modal-body {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin: auto;
    width: 98.5%;
}

.menu-input-num {
    margin: 0.75em;
    padding: 1em;
    margin: 0.5em;
    font-size: 14px;
    border-radius: 5px;
    border: 2px solid #ccc;
    min-width: 40%;
}
